
.content{
  position: relative;
  top:0;
  overflow: auto;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 100%;
  min-width:40rem;
}
.animate-enter{
  -webkit-transform: translate(100%,0);
          transform: translate(100%,0);
  opacity: 0;
}
.animate-leave{
  translate: translate(-100%,0);
  opacity: 0;
}
