}

.footer{
  width:100%;
  background:#222222;
  padding-bottom:0.5rem;
}
.footer .line-foot{
  width:100%;
  height:0.46rem;
  background:#17375f;
}
.footer .foot-con{
  width:48rem;
  height:9rem;
  margin:0 auto;
  overflow: hidden;
}
.footer .foot-con .foot-left{
  float: left;
}
.footer .foot-con .foot-left h4{
